import React, {useState} from 'react'
import {checkAgeAC, homeWorkReducer, sortDownAC, sortUpAC} from './bll/homeWorkReducer'
import SuperButton from '../h4/common/c2-SuperButton/SuperButton'
import classes from './HW8.module.css'

export type UserType = {
    _id: number
    name: string
    age: number
}

const initialPeople = [
    {_id: 0, name: '–ö–æ—Ç', age: 3},
    {_id: 1, name: '–ê–ª–µ–∫—Å–∞–Ω–¥—Ä', age: 66},
    {_id: 2, name: '–ö–æ–ª—è', age: 16},
    {_id: 3, name: '–í–∏–∫—Ç–æ—Ä', age: 44},
    {_id: 4, name: '–î–º–∏—Ç—Ä–∏–π', age: 40},
    {_id: 5, name: '–ò—Ä–∏–Ω–∞', age: 55},
]

function HW8() {
    const [people, setPeople] = useState<UserType[]>(initialPeople) // need to fix any

    // need to fix any
    const finalPeople = people.map((p: UserType) => (
        <div key={p._id} className={classes.item}>
            <span>{`name: ${p.name}`}</span>
            {p.age}
        </div>
    ))

    const sortUp = () => setPeople(homeWorkReducer(initialPeople, sortUpAC()))
    const sortDown = () => setPeople(homeWorkReducer(initialPeople, sortDownAC()))
    const checkAge = () => setPeople(homeWorkReducer(initialPeople, checkAgeAC()))

    return (
        <div>
            <hr/>
            homeworks 8

            {/*should work (–¥–æ–ª–∂–Ω–æ —Ä–∞–±–æ—Ç–∞—Ç—å)*/}
            {finalPeople}

            <div><SuperButton onClick={sortUp}>sort up</SuperButton></div>
            <div><SuperButton onClick={sortDown}>sort down</SuperButton></div>
            <div><SuperButton onClick={checkAge}>check 18</SuperButton></div>

            <hr/>
            {/*–¥–ª—è –ª–∏—á–Ω–æ–≥–æ —Ç–≤–æ—Ä—á–µ—Å—Ç–≤–∞, –º–æ–≥—É –ø—Ä–æ–≤–µ—Ä–∏—Ç—å*/}
            {/*<AlternativePeople/>*/}
            <hr/>
        </div>
    )
}

export default HW8
